<template>
  <div class="big">
    <div id="particles">
      <div class="demo-ruleForm">
        <div class="baseInput use_input">
          <label for="">用户名：</label>
          <au-input class="one"
          type="text"
          @handleBlur="onUser"
          ></au-input>
          <p style="color:red">{{userName}}</p>
        </div>
        <div class="baseInput use_pwd">
          <label for="">密&nbsp;&nbsp;&nbsp;码：</label>
          <au-input class="one"
          type="password"
          @handleBlur="onPwdBlur"
          ></au-input>
          <p style="color:red">{{pass}}</p>
        </div>
        <el-button :plain="true" @click="open" class="one1">提交</el-button>
        <el-button :plain="true" @click="openVn" class="one1">重置</el-button>
      </div>
      </div>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        userName: '',
        pass: ''
      }
    },
    computed: {
      count () {
        return this.$store.state.count
      }
    },
    mounted () {
      particlesJS.load('particles', '../../static/data/particles.json')
    },
    methods: {
      onUser (val) {
        let reg = /\d/;
        if(!reg.test(val)) {
          this.userName="用户名有误"
        }
      },
      onPwdBlur (val) {
        let reg = /^\d{6}$/
        if(!reg.test(val)) {
          this.pass="密码有误"
        }
      },
      open() {
        this.$message('这是一条消息提示')
      },
      openVn() {
        const h = this.$createElement
        this.$message({
          message: h('p',null,[
            h('span',null,'内容可以是'),
            h('i',{style:'color: teal'},'VNode')
          ])
        })
      }
    }
  }
</script>
<style>
body,html{
  width:100%;
  height:100%;
}
*{
  padding:0;
  margin:0;
  list-style:none;
}
.big {
  width:100%;
  height:100%;
  background-image: url("../assets/bg.jpg");
  background-repeat:no-repeat;
  background-size:100% 100%;
}
.baseInput{
  display:flex;
  margin:18px 0;
}
.demo-ruleForm{
  position:absolute;
  top:50%;
  left:50%;
  transform:translate(-50%,-50%);
}
.demo-ruleForm{
  text-align:center;
  background:rgba(0,0,0,.5);
  padding:40px;
}
</style>



