<template>
  <div>
    <input 
    :type="type"
    :class="className"
    @input = "handleInput"
    @focus = "handleFocus"
    @blur = "handleBlur"
    >
  </div>
</template>
<script>
  export default {
    name: 'Input',
    props: {
      type: {
        type: String,
        default: 'text'
      },
      placeholder: {
        type: String,
        default: ''
      },
      index: {
        type: Number,
        default: 0
      },
      value: {},
      maxLength: {
        type: Number,
        default: 50
      },
      className: {
        type: String,
        default: ''
      }
    },
    methods: {
      handleInput(e) {
        this.$emit('handleInput',e.target.value)
      },
      handleFocus(e) {
        this.$emit('handleFocus',e.target.value)
      },
      handleBlur(e) {
        this.$emit('handleBlur',e.target.value)
      }
    }
  }
</script>


